/*
font-family: 'Abel', sans-serif;
*/

* {
  border: 1px solid #8f8;
  background-color: #000;
  color: #0f0;
  font-family: monospace;
  margin: 1px;
  padding: 1px;
  text-align: center;
  width: auto;
  height: auto;
}

a {
  border: none;
}

#editor {
  border: 1px solid blue;
  font-size: 13px;
  text-align: left;
  width: 85vw;
}

#preview {
  border: 1px solid red;
  overflow-x: auto;
}

#preview * {
  border: none;
  font-family: 'Abel', sans-serif;
  color: #8f8;
  text-align: left;
}

#preview code {
  font-family: monospace;
  font-weight: 600;
  color: #0f0;
}

#preview pre {
  margin-left: 20px;
  padding: 5px;
  background-color: #050505;
  overflow-x: auto;
  border-radius: 5px;
}

#preview pre code {
  font-weight: 100;
  background-color: #050505;
  color: #0f0;
}



